import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../../services/commonService";
import { MaintenanceService } from "../../services/maintenanceService";
import swal from 'sweetalert2';

declare var $: any;

@Component({
    selector: 'app-maintenance',
    templateUrl: './maintenance.component.html',
    styleUrls: ['./maintenance.component.scss'],
    providers: [CommonService, MaintenanceService]
})
export class MaintenanceMasterComponent implements OnInit {

    public formMaintenance = new FormGroup({
        title: new FormControl(),
        description: new FormControl(),
        chkActive: new FormControl()
    });

    public formPartMaintenance = new FormGroup({
        title: new FormControl(),
        description: new FormControl(),
        chkActive: new FormControl()
    });

    public mTypeList: any;
    public mPartTypeList: any;

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private maintenanceService: MaintenanceService,
        private activatedRoute: ActivatedRoute,
        private formBuilder: FormBuilder
    ) { }

    showSwal(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'

        }).catch(swal.noop);
    }

    isFieldValid(form: FormGroup, field: string) {
        return !form.get(field).valid && form.get(field).touched;
    }

    displayFieldCss(form: FormGroup, field: string) {
        return {
            'has-error': this.isFieldValid(form, field),
            'has-feedback': this.isFieldValid(form, field)
        };
    }

    ngOnInit() {
        this.getMaintenanceType();
        this.getMaintenancePartType();

        this.formMaintenance = this.formBuilder.group({
            title: ['', Validators.required],
            description: ['', Validators.required],
            chkActive: ['', Validators.required]
        });

        this.formPartMaintenance = this.formBuilder.group({
            title: ['', Validators.required],
            description: ['', Validators.required],
            chkActive: ['', Validators.required]
        });
    }

    resetForm() {
        localStorage.removeItem("MTypeID");
        this.formMaintenance.reset();
        this.formMaintenance.controls["chkActive"].setValue(true);
    }
    resetPartForm() {
        localStorage.removeItem("MPTypeID");
        this.formPartMaintenance.controls["chkActive"].setValue(true);
        this.formPartMaintenance.reset();
    }

    createMaintenanceType() {
        if (this.formMaintenance.valid) {
            $('.loader').show();
            var contact = {
                ID: localStorage.getItem("MTypeID"),
                Title: this.formMaintenance.controls["title"].value,
                Description: this.formMaintenance.controls["description"].value,
                IsActive: this.formMaintenance.controls["chkActive"].value
            }
            return this.maintenanceService.CreateMaintenanceType(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataMtp = JSON.stringify(data);
                    var mTpParse = JSON.parse(dataMtp);
                    var mTpList = JSON.parse(mTpParse["_body"]);
                    if (mTpList == true) {
                        $('.loader').hide();
                        $('#myModal').modal('toggle');
                        this.getMaintenanceType();
                        if (localStorage.getItem("MTypeID") != '' && localStorage.getItem("MTypeID") != null) {
                            this.showSwal("Maintenance Type updated successfully!");
                        }
                        else {
                            this.showSwal("Maintenance Type created successfully!");
                        }
                    }
                }
            );
        } else {
            $('.loader').hide();
            this.validateAllFormFields(this.formMaintenance);
        }
    }

    createMaintenancePartType() {
        if (this.formPartMaintenance.valid) {
            $('.loader').show();
            var contact = {
                ID: localStorage.getItem("MPTypeID"),
                Title: this.formPartMaintenance.controls["title"].value,
                Description: this.formPartMaintenance.controls["description"].value,
                IsActive: this.formPartMaintenance.controls["chkActive"].value
            }
            return this.maintenanceService.CreateMaintenancePartType(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataMtp = JSON.stringify(data);
                    var mTpParse = JSON.parse(dataMtp);
                    var mTpList = JSON.parse(mTpParse["_body"]);
                    if (mTpList == true) {
                        $('.loader').hide();
                        $('#myPartModal').modal('toggle');
                        this.getMaintenancePartType();
                        if (localStorage.getItem("MPTypeID") != '' && localStorage.getItem("MPTypeID") != null) {
                            this.showSwal("Maintenance Part Type updated successfully!");
                        }
                        else {
                            this.showSwal("Maintenance Part Type created successfully!");
                        }
                    }
                }
            );
        } else {
            $('.loader').hide();
            this.validateAllFormFields(this.formPartMaintenance);
        }

    }

    getMaintenanceType() {
        $('.loader').show();
        return this.maintenanceService.GetMaintenanceType(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMtp = JSON.stringify(data);
                var mTpParse = JSON.parse(dataMtp);
                var mTpList = JSON.parse(mTpParse["_body"]);
                if (mTpList != null) {
                    this.mTypeList = mTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    getMaintenancePartType() {
        $('.loader').show();
        return this.maintenanceService.GetMaintenancePartType(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMptp = JSON.stringify(data);
                var mpTpParse = JSON.parse(dataMptp);
                var mpTpList = JSON.parse(mpTpParse["_body"]);
                if (mpTpList != null) {
                    this.mPartTypeList = mpTpList;
                    $('.loader').hide();
                }
            }
        );
    }

    viewMaintenanceType(mTypeID: any) {
        localStorage.removeItem("MTypeID");
        var elems = document.querySelectorAll(".label-floating");
        if (elems != null) {
            [].forEach.call(elems, function (el) {
                el.classList.remove("is-empty");
            });
        }
        var mType = this.mTypeList.filter(item => item.ID == mTypeID);
        localStorage.setItem("MTypeID", mType[0].ID);
        this.formMaintenance.controls["title"].setValue(mType[0].Title);
        this.formMaintenance.controls["description"].setValue(mType[0].Description);
        this.formMaintenance.controls["chkActive"].setValue(mType[0].IsActive);
    }

    viewMaintenancePartType(mPTypeID: any) {
        localStorage.removeItem("MPTypeID");
        var elems = document.querySelectorAll(".label-floating");
        if (elems != null) {
            [].forEach.call(elems, function (el) {
                el.classList.remove("is-empty");
            });
        }
        var mType = this.mPartTypeList.filter(item => item.ID == mPTypeID);
        localStorage.setItem("MPTypeID", mType[0].ID);
        this.formPartMaintenance.controls["title"].setValue(mType[0].Title);
        this.formPartMaintenance.controls["description"].setValue(mType[0].Description);
        this.formPartMaintenance.controls["chkActive"].setValue(mType[0].IsActive);
    }

    deleteMaintenanceType(mTypeID: any) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            this.maintenanceService.DeleteMaintenanceType(this.commonService.baseApiUrl, mTypeID).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo == true) {
                        this.getMaintenanceType();
                        $('.loader').hide();
                        swal({
                            title: 'Deleted!',
                            text: 'Maintenance Type been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                });
        }).catch(swal.noop);

    }

    deleteMaintenancePartType(mTypeID: any) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            this.maintenanceService.DeleteMaintenancePartType(this.commonService.baseApiUrl, mTypeID).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo == true) {
                        this.getMaintenancePartType();
                        $('.loader').hide();
                        swal({
                            title: 'Deleted!',
                            text: 'Maintenance Part Type been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                });
        }).catch(swal.noop);


    }

    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }
}



